import moment from 'moment';
import 'moment/locale/id';
import { format } from 'date-fns';
import momentTimezone from 'moment-timezone';

export function convertLongDateToIndo(epochTime) {
  return moment(epochTime).locale('id').format('dddd, DD MMMM YYYY');
}

export function convertEpochMomentToIndo(epochTime) {
  return moment.unix(epochTime).format("D MMMM YYYY [pukul] HH.mm");
}

export function convertEpochToIndo(epochTime) {
  return format(new Date(epochTime * 1000), 'dd-MM-yyyy');
}

export function indonesianTime(time) {
  return momentTimezone.utc(time, 'HHmm').tz('Asia/Jakarta').format('HH:mm');
}

export const formatDateToIndonesian = (dateString) => {
  const options = { year: 'numeric', month: 'long', day: 'numeric' };
  const date = new Date(dateString);
  return date.toLocaleDateString('id-ID', options);
};

export function indonesianDate(data: any) {
  return moment(data).locale('id').format('dddd, DD MMMM YYYY');
}
