import {apiBaseUrl} from "../../../apiBaseUrl";
import {getError, getResponseError} from '../../../helper/handlingError';
import {
    REQUEST_DETAIL_EVENT,
    REQUEST_DETAIL_EVENT_FAILURE,
    REQUEST_DETAIL_EVENT_SUCCESS,
    REQUEST_EVENT_ACTIVE,
    REQUEST_EVENT_ACTIVE_FAILED,
    REQUEST_EVENT_ACTIVE_SUCCESS,
    REQUEST_GET_EVENT_LIST,
    REQUEST_GET_EVENT_LIST_FAILURE,
    REQUEST_GET_EVENT_LIST_SUCCESS,
    REQUEST_REGISTRATION_EVENT,
    REQUEST_REGISTRATION_EVENT_FAILURE,
    REQUEST_REGISTRATION_EVENT_SUCCESS
} from "../../../constant/EventConstant";

export const fetchAllEventAction = (requestParam) => {
    return async (dispatch: any) => {
        dispatch({
            type: REQUEST_GET_EVENT_LIST,
        });
        try {
            const {data, status} = await apiBaseUrl({
                method: 'GET',
                params: {
                    status: requestParam.status,
                    categoryEventId: requestParam.categoryEventId,
                    page: requestParam.page,
                    size: requestParam.size,
                },
                headers: {
                    'Content-Type': 'application/json',
                },
                data: {},
                url: `/public/api/v2/event`,
            });

            if (data.result == null) {
                dispatch({
                    type: REQUEST_GET_EVENT_LIST_FAILURE,
                });
                return getResponseError(data.responseData.message);
            }
            if (status === 200) {
                dispatch({
                    type: REQUEST_GET_EVENT_LIST_SUCCESS,
                    payload: data,
                });
                return data.result;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_GET_EVENT_LIST_FAILURE,
            });
            await getError(error);
        }
    };
};

