export const fetchEventActive = (page: number) => {
    return async (dispatch: any) => {
        dispatch({
            type: REQUEST_EVENT_ACTIVE,
        });
        try {
            const { data, status } = await apiBaseUrl({
                method: 'GET',
                params: {
                    page,
                    size: 1
                },
                headers: {
                    'Content-Type': 'application/json',
                },
                url: '/public/api/v2/event/active',
                data: {}
            });

            if (data.responseData.code === 204) {
                dispatch({
                    type: REQUEST_EVENT_ACTIVE_SUCCESS,
                    payload: data.responseData.code,
                });
                return [];
            }

            if (status === 200) {
                dispatch({
                    type: REQUEST_EVENT_ACTIVE_SUCCESS,
                    payload: data,
                });
                return data;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_EVENT_ACTIVE_FAILED,
            });
            await getError(error);
        }
    };
};
export const fetchRegistrationEvent = (requestParam) => {
        return async (dispatch: any) => {
        dispatch({
            type: REQUEST_REGISTRATION_EVENT,
        });
        try {
            const formData = new FormData();
            formData.append('email', requestParam.email);
            formData.append('riderName', requestParam.riderName);
            formData.append('nickname', requestParam.nickname);
            formData.append('noStart', requestParam.noStart);
            formData.append('parentName', requestParam.parentName);
            formData.append('noTelp', requestParam.noTelp);
            formData.append('masterCategoryRaceId', requestParam.masterCategoryRaceId);
            formData.append('eventId', requestParam.eventId);
            formData.append('community', requestParam.community);
            formData.append('dob', requestParam.dob);
            formData.append('filePayment', requestParam.filePayment);

            const { data, status } = await apiBaseUrl({
                method: 'POST',
                data: formData,
                headers: {
                    'Content-Type': 'application/form-data',
                },
                url: `/public/api/v2/event/registration/add`,
            });
            if (status === 200) {
                dispatch({
                    type: REQUEST_REGISTRATION_EVENT_SUCCESS,
                    payload: data,
                });
                return data;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_REGISTRATION_EVENT_FAILURE,
            });
            await getError(error);
        }
    };
}
