import {Container, Pagination} from "@mui/material";
import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {fetchAllEventAction} from "../../../app/service/action/public/event/eventAction";
import Loading from "../../../components/Loading/Loading";
import BannerEvent from "../../../components/event/BannerEvent";
import TitleComponent from "../../../components/Title/TitleComponent";

const INITIAL_OBJECT = {
    size: 5,
    page: 1,
    status: 1, //1 =>> external || 0 => internal
    categoryEventId: 1, //1 => latber || 2 => race
}
export const Event = () => {
    const dispatch: any = useDispatch();
    const [requestParam, setRequestParams] = useState(INITIAL_OBJECT)

    const isLoading = useSelector(({eventListState}) => eventListState.eventList.isLoading)
    const data = useSelector(({eventListState}) => eventListState.eventList.data)

    useEffect(() => {
        // @ts-ignore
        dispatch(fetchAllEventAction(requestParam));
    }, [dispatch, requestParam]);
    const handleChange = (event, value: number) => {
        setRequestParams({...requestParam, page: value})
    };
    return (
        <Container>
            {isLoading ? (
                <Loading/>
            ) : (
                <div className={'box-content'}>
                    <TitleComponent title={'Daftar Event Pushbike Tangerang'}/>
                    {data.result ? data.result.map((item) => {
                        return (
                            <div className={'banner'} key={item.id}>
                                <BannerEvent
                                    id={item.id}
                                    image={item.image}
                                    name={item.name}
                                    price={item.price}
                                    date={item.date}/>
                            </div>
                        )
                    }) : ('')}
                    <div className={'event-pagination'}>
                        <Pagination count={data.result ? data.metaData.totalPage : 0}
                                    page={requestParam.page}
                                    color={'primary'}
                                    onChange={handleChange}/>
                    </div>
                </div>
            )}
        </Container>
    )
}
