import {useNavigate} from "react-router-dom";
import checkAuth from "../../app/auth";
import {convertLongDateToIndo} from "../../utils/DateUtils";
import noImage from "../../assets/image/No-Image-Placeholder.svg.webp"

const BannerEvent = ({id, image, name, date, price}) => {
    const navigate = useNavigate();
    const token = checkAuth()
    const userDetail = JSON.parse(localStorage.getItem('userDetail'));

    function handleClick(id) {
        if (token && userDetail) {
            if (userDetail.scope === "Administrator") {
                navigate(`/app/event/${id}`)
            } else if (userDetail.scope === "Member") {
                navigate(`/member/event/${id}`)
            }
        } else {
            navigate(`/event/${id}`)
        }
    }

    return (
        <div className={'event-banner'} key={id}>
            <div className={'event-banner-image'}>
                {image ? (
                    <img src={image} alt="banner"/>
                ) : (
                    <img src={noImage} alt=""/>
                )}
            </div>
            <div className={'event-banner-note'}>
                <div>
                    <div className={'event-title'}>{name}</div>
                    <div className={'event-date'}>{convertLongDateToIndo(date)}</div>
                </div>
                {/*<div className={'event-fee'}>Registration <span*/}
                {/*    className={'fee'}>{price ? formatRupiah(price) : 'FREE'}</span></div>*/}
                <div className={'event-button'}>
                    <button onClick={() => handleClick(id)}>
                        Detail Information
                    </button>
                </div>
            </div>
        </div>
    )
}

export default BannerEvent;
